import React, { PureComponent } from "react";
import { CloudUrl, WebUrl } from "../../../../constants";
import { isTagbox, isTagShop } from "../../../../actions/api";
import { brandingUrl } from "../../../../utils/Common";

function importantCss(el) {
  el.style.setProperty('visibility', 'visible', 'important');
  el.style.setProperty('opacity', '1', 'important');
  el.style.setProperty('display', 'flex', 'important');
  el.style.setProperty('transform', 'none', 'important');
  el.style.setProperty('clip-path', 'initial', 'important');
  el.style.setProperty('border', 'none', 'important');
}
export default class ThemeBranding extends PureComponent {
  render() {
    const { containerSize,brandingNetworks, wallId, wall, user } = this.props    
    const brandingUrlWithUTM = isTagShop  ? `${WebUrl}` : `${brandingUrl(brandingNetworks, wallId)[0]}`
    const logoName = user == "tagembed" ? 'tagembed.svg' : isTagShop ? 'tagshop.svg' : isTagbox || user == "taggbox" ? 'taggbox.svg' : ''
    const { poweredBy } = wall.ThemeStyle.language.text
    return (
      <div ref={(el) => {
        if (el) {
          el.style.setProperty('align-items', 'center', 'important');
          el.style.setProperty('padding', '2px 8px', 'important');
          el.style.setProperty('justify-content', 'end', 'important');
          el.style.setProperty('width', '100%', 'important');
          el.style.setProperty('height', 'auto', 'important');
          el.style.setProperty('margin-top', '3px', 'important');
          el.style.setProperty('position', 'static', 'important');
          importantCss(el)
        }
      }}>
        <a href={brandingUrlWithUTM} target={'_blank'}
          ref={(el) => {
            if (el) {
              el.style.setProperty('background-color', 'rgba(255, 255, 255, .9)', 'important');
              el.style.setProperty('background', 'none', 'important');
              el.style.setProperty('border-radius', '3px', 'important');
              el.style.setProperty('align-items', 'center', 'important');
              el.style.setProperty('display', 'flex', 'important');
              el.style.setProperty('width', 'auto', 'important');
              el.style.setProperty('text-decoration', 'none', 'important');
              el.style.setProperty('direction', 'ltr', 'important');
              el.style.setProperty('position', 'static', 'important');
              importantCss(el)
              if (containerSize.width < 480) {
                el.style.setProperty('padding', '4px', 'important');
              } else {
                el.style.setProperty('padding', '6px', 'important');
              }
            }
          }}>
          <div ref={(el) => {
            if (el) {
              el.style.setProperty('color', '#585858', 'important');
              el.style.setProperty('width', '100%', 'important');
              el.style.setProperty('height', 'auto', 'important');
              el.style.setProperty('position', 'static', 'important');
              el.style.setProperty('font-family', 'inherit', 'important');
              importantCss(el)
              if (containerSize.width < 480) {
                el.style.setProperty('font-size', '12px', 'important');
                el.style.setProperty('margin-right', '4px', 'important');
              } else {
                el.style.setProperty('font-size', '14px', 'important');
                el.style.setProperty('margin-right', '6px', 'important');
              }
            }
          }}>{ poweredBy ? poweredBy : 'Powered by'}</div>
          <div ref={(el) => {
            if (el) {
              el.style.setProperty('background-image', `url(${CloudUrl}/media/logo/${logoName})`, 'important');
              el.style.setProperty('background-size', 'contain', 'important');
              el.style.setProperty('background-position', 'left center', 'important');
              el.style.setProperty('background-repeat', 'no-repeat', 'important');
              el.style.setProperty('position', 'static', 'important');
              importantCss(el)
              if (containerSize.width < 480) {
                el.style.setProperty('height', '22px', 'important');
                el.style.setProperty('min-height', '22px', 'important');
                el.style.setProperty('width', '82px', 'important');
                el.style.setProperty('min-width', '82px', 'important');
              } else {
                el.style.setProperty('height', '26px', 'important');
                el.style.setProperty('min-height', '26px', 'important');
                el.style.setProperty('width', '96px', 'important');
                el.style.setProperty('min-width', '96px', 'important');
              }
            }
          }}></div>
        </a>
      </div>
    );
  }
}
